import React from 'react';
import Tilt from 'react-tilt';
import violin from './violin.png';
import './Logo.css';

const Logo = () => {
    return (
        <div className='ml4'>
            <Tilt className="Tilt br2 shadow-2 flex items-center" options={{ max : 25 }} style={{ height: 80, width: 300 }} >
                <div className="Tilt-inner mr2 ml2"> <img alt='logo' src={violin}></img> </div>
            </Tilt>
        </div> 
    )
}

export default Logo;